$font-stack: 'Raleway-Light',
sans-serif;
$dominant-color: blue;
$light-color: lightblue;
$light-color-dark: rgb(0, 132, 255);
$very-light-color: white;
$page-background:white;
$page-background-dark: #202020;
$page-color-dark: #f0f0f0;
html {
    margin: 1em;
    font-size: 1em;
}

body {
    background-color: $page-background;
}

.post-frame h2,h3,h4,h5,h6 {
    margin-top: 1em;
    border-top: 1px solid $light-color-dark;
}

table {
    // font-family: Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
    margin-top: 2em;
}

table td,
table th {
    border: 1px solid #ddd;
    padding: 8px;
}

table tr:nth-child(even) {
    background-color: #f2f2f2;
}

table tr:hover {
    background-color: #ddd;
}

table th {
    padding-top: 12px;
    padding-bottom: 12px;
    text-align: left;
    background-color: $light-color;
    color: black;
}

.date {
    text-decoration: underline $dominant-color;
    margin-bottom: 0.3em;
    //margin-left: 1em;
}

.nav {
    border-bottom: 1px solid $dominant-color;
    margin-bottom: 1.2em;
    padding-bottom: 1em;
    padding: 0.5em;
    margin-left: 0em;
    margin-right: 0em;
    background-color: $very-light-color;
}

.postexcerpt,
.post-frame,
.tag-frame,
.imprint {
    border-left: 0.1em solid $dominant-color;
    box-shadow: 0em 0em 0.15em gray;
    background-color: $very-light-color;
    padding: 0.1em 1em 0.1em 1em;
    margin-bottom: 0.5em;
    background-position: bottom;
    background-repeat: repeat-x;
    /*background-image: url("/assets/img/bottomframe.svg"); */
}

.tags {
    margin-top: 0.5em;
    padding-top: 1em;
    //border-top: 1px solid $dominant-color;
}

.tag {
    //border: 1px solid blue;
    padding: 0.25em;
    margin-right: 0.5em;
    //background-color: rgb(122, 224, 255);
    border-bottom: 1px solid $dominant-color;
}

.highlighter-rouge {
    border: 1px solid $light-color;
    margin: 0em;
    padding: 0.3em;
}

.highlighter-rouge .highlight {
    margin: 0;
    padding: 0;
}

.container {
    font-family: $font-stack;
    font-size: 1.2em;
    padding: 0em;
}

// Postexcerpt
.postexcerpt .title {
    margin-bottom: 0em;
}

.post .title {
    margin-bottom: 0;
}

// 404
.fourzerofour {
    margin: 1em auto;
    text-align: center;
}

.fourzerofour h1 {
    margin: 0.5em 0;
    font-size: 5em;
    line-height: 1;
    letter-spacing: -1px;
}

// Pagination
.pagination {
    border-top: 1px solid $dominant-color;
    border-radius: 0;
    margin-top: 1.2em;
    width: 100%;
}

.pagination .previous {
    float: left;
    width: 100px;
    text-align: left;
}

.pagination .next {
    float: right;
    width: 100px;
    text-align: right;
}

.pagination .page_number {
    margin: 0 auto;
    text-align: center;
}

#footer {
    text-align: center;
    font-size: 0.75em;
}

.pagenavigation {
    display: block;
    width: auto;
    overflow: hidden;
    border-top: 1px solid $dominant-color;
    margin-top: 1em;
    padding-top: 0.25em;
}

.pagenavigation .prev {
    text-align: left;
    float: left;
}

.pagenavigation .next {
    text-align: right;
    float: right;
}

.image {
    border: 1px solid $light-color;
    background-color: $very-light-color;
    padding: 1em;
    padding-bottom: 0.25em;
    max-width: 90%;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 1em;
}

.image img {
    width: 100%;
}

.image .caption {
    font-style: italic;
    text-align: right;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
}

@media (prefers-color-scheme: dark) {
    body,
    .nav,
    .postexcerpt,
    .post-frame,
    .tag-frame,
    .imprint {
        background-color: $page-background-dark;
        color: $page-color-dark;
    }

    .post-frame h2,h3,h4,h5,h6 {
        margin-top: 1em;
        border-top: 1px solid $light-color;
    }

    pre {
        color: $page-color-dark;
    }
    table tr:nth-child(even) {
        background-color: #4a4a4a;
    }
    table th {
        padding-top: 12px;
        padding-bottom: 12px;
        text-align: left;
        background-color: $page-background-dark;
        color: $page-color-dark;
    }
    table tr:hover {
        background-color: $page-background-dark;
    }
    .postexcerpt,
    .post-frame,
    .tag-frame,
    .imprint {
        /* background-image: url("/assets/img/bottomframe_dark.svg"); */
        border-left: 1px solid $light-color-dark;
    }
    .nav {
        border-bottom: 1px solid $light-color-dark;
        .date {
            text-decoration: underline $light-color-dark;
        }
    }
    .image {
        border: 1px solid $light-color;
        background-color: $page-background-dark;
    }
}